import * as React from 'react';
import { Container, Button, Grid } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import Avatar from '@material-ui/core/Avatar';
import 'fontsource-roboto';


interface WordProps {
    word: string;
    partOfSpeech: string;
    trans: string;
    visibleTrans: string;
    isInputWordCompleted: boolean;
    inputWordString:string
    onClick: () => void;
    changeInputWordCompleted: (val:boolean) => void;
    changeInputWord: (val:string) => void;    
}

class Word extends React.Component<WordProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }

    const synth = window.speechSynthesis;
    const voices = synth.getVoices().filter(voice => voice.lang === 'en-US');
    const voice = voices[0];
    const partOfSpeech = this.props.partOfSpeech.length ? this.props.partOfSpeech : '';

    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <div onClick={() => this.props.onClick()}>
            <Avatar>{partOfSpeech.substr(0,1)}</Avatar>
            <p className="word">{this.props.trans}</p>
          </div>
          <Typography component="div">
            <Box
              className={this.props.visibleTrans}
              fontSize="h5.fontSize" m={1}
            >
              {this.props.word}
            </Box>
          </Typography>
            <div>
              <TextField
                error={!this.props.isInputWordCompleted}
                label= {this.props.isInputWordCompleted ? "OK" : "input word"}
                fullWidth={true}
                value={this.props.inputWordString}
                variant="outlined"
                onChange={(o) => 
                  {
                    this.props.changeInputWord(o.target.value.toLowerCase());
                    if (this.props.word.toLowerCase() === o.target.value.toLowerCase()) {
                      this.props.changeInputWordCompleted(true);
                      this.props.onClick();
                      const utter = new SpeechSynthesisUtterance();
                      utter.text = 'OK.' + this.props.word;
                      utter.lang = "en-US";
                      utter.voice = voice;
                      window.speechSynthesis.speak(utter);
    
                    } else {
                      this.props.changeInputWordCompleted(false);
                    }
                  }
                }
              />
            </div>
          
          <br/>
          <Grid
            container
            direction="row"
            spacing={5}
            justify="center"
            alignItems="center"
          >
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                color="primary"
                onClick={() => this.props.onClick()}
              >
                english
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                color="secondary"
                onClick={() => {
                  const utter = new SpeechSynthesisUtterance();
                  utter.text = this.props.word;
                  utter.lang = "en-US";
                  utter.voice = voice;
                  window.speechSynthesis.speak(utter);
                }}
              >
                play
              </Button>
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default Word;