import * as React from 'react';
import { Container, Button, Grid } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import 'fontsource-roboto';


interface SentenceProps {
    word: string;
    trans: string;
    visibleTrans: string;
    onClick: () => void;
}

class Sentence extends React.Component<SentenceProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }

    const synth = window.speechSynthesis;
    const voices = synth.getVoices().filter(voice => voice.lang === 'en-US');
    const voice = voices[0];

    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <div onClick={() => this.props.onClick()}>
            <p className="word">{this.props.trans}</p>
          </div>
          <Typography component="div">
            <Box
              className={this.props.visibleTrans}
              fontSize="h6.fontSize" m={1}
            >
              {this.props.word}
            </Box>
          </Typography>
          <Grid
            container
            direction="row"
            spacing={5}
            justify="center"
            alignItems="center"
          >
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                color="primary"
                onClick={() => this.props.onClick()}
              >
                english
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                color="secondary"
                onClick={() => {
                  const utter = new SpeechSynthesisUtterance();
                  utter.text = this.props.word;
                  utter.lang = "en-US";
                  utter.voice = voice;
                  utter.rate = 0.8;
                  window.speechSynthesis.speak(utter);
                }}
              >
                play
              </Button>
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default Sentence;