import React, { useState } from 'react';
import { Drawer, Switch, IconButton, List, ListItem } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';

const Menu: React.FC = ({}, {}) => {
  const [menuToggle, setMenuToggle] = useState(false);
  const [checkA, setCheckA] = useState(false);
  const [themeMode, setThemeMode] = useState('dark');

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  const changeThemeMode = () => {
    setCheckA(!checkA);
    if (checkA) {
      setThemeMode('light');
    } else {
      setThemeMode('dark');
    }
  }

  return (
    <>
      <IconButton
        color="inherit"
        aria-label="open drawer"
        edge="end"
        onClick={() => toggleDrawer()}
      >
    </IconButton>
      <Drawer
        anchor = {'left'}
        open = {menuToggle}
        onClose = {() => setMenuToggle(false)}
      >
        <div>
          <List>
            <ListItem>
              Theme Mode
              <Switch
                edge="end"
                checked={checkA}
                onChange={changeThemeMode}
                color="primary"
                name="checkA"
                inputProps={{ 'aria-label': 'switch-list-label-theme-mode' }}
              />
            </ListItem>
          </List>
        </div>
      </Drawer>
      <MenuIcon />
    </>
  );
}

export default Menu;